import { RPTooltip } from "@rp/react/web/common/base-components"
import { IconButton } from "@rp/react/web/mui/core"
import { Feedback } from "@rp/react/web/mui/icons"
import { useFeedbackContext } from "./FeedbackContext"

export const FeedbackButton = () => {
  const { isFeedbackMode, setOpen } = useFeedbackContext()

  // Open feedback drawer
  const handleDrawer = (state: boolean) => () => {
    setOpen(state)
  }

  if (!isFeedbackMode) return null

  return (
    <RPTooltip title="Feedback" placement="bottom">
      <IconButton role="feedback-button" onClick={handleDrawer(true)} size="large">
        <Feedback />
      </IconButton>
    </RPTooltip>
  )
}
